import React, { createContext, useState, useContext, useEffect } from 'react';

// HEX to HSL conversion function
function hexToHSL(hex) {
    let r = 0, g = 0, b = 0;
    if (hex.length === 7) {
        r = parseInt(hex.slice(1, 3), 16);
        g = parseInt(hex.slice(3, 5), 16);
        b = parseInt(hex.slice(5, 7), 16);
    }
    r /= 255; g /= 255; b /= 255;
    let max = Math.max(r, g, b), min = Math.min(r, g, b);
    let h, s, l = (max + min) / 2;

    if (max === min) {
        h = s = 0;
    } else {
        let d = max - min;
        s = l > 0.5 ? d / (2 - max - min) : d / (max + min);
        switch (max) {
            case r: h = (g - b) / d + (g < b ? 6 : 0); break;
            case g: h = (b - r) / d + 2; break;
            case b: h = (r - g) / d + 4; break;
            default: h = 0; // Default value set karo
        }
        h = Math.round(h * 60);
    }
    return [h, Math.round(s * 100), Math.round(l * 100)];
}

// HSL to HEX conversion function
function HSLToHex(h, s, l) {
    s /= 100; l /= 100;
    let c = (1 - Math.abs(2 * l - 1)) * s;
    let x = c * (1 - Math.abs((h / 60) % 2 - 1));
    let m = l - c / 2;
    let r = 0, g = 0, b = 0;

    if (h >= 0 && h < 60) { r = c; g = x; b = 0; }
    else if (h >= 60 && h < 120) { r = x; g = c; b = 0; }
    else if (h >= 120 && h < 180) { r = 0; g = c; b = x; }
    else if (h >= 180 && h < 240) { r = 0; g = x; b = c; }
    else if (h >= 240 && h < 300) { r = x; g = 0; b = c; }
    else if (h >= 300 && h < 360) { r = c; g = 0; b = x; }

    r = Math.round((r + m) * 255).toString(16).padStart(2, '0');
    g = Math.round((g + m) * 255).toString(16).padStart(2, '0');
    b = Math.round((b + m) * 255).toString(16).padStart(2, '0');

    return `#${r}${g}${b}`;
}

const ColorContext = createContext();

const ColorProvider = ({ children }) => {
    const [primaryColor, setPrimaryColor] = useState("#133155"); // Default Tailwind blue-500

    useEffect(() => {
        const root = document.documentElement;
        const [h, s] = hexToHSL(primaryColor);

        // Dynamically generate shades
        const shades = {
            50: HSLToHex(h, s, 95),
            100: HSLToHex(h, s, 88),
            200: HSLToHex(h, s, 76),
            300: HSLToHex(h, s, 64),
            400: HSLToHex(h, s, 52),
            500: primaryColor,
            600: HSLToHex(h, s, 40),
            700: HSLToHex(h, s, 30),
            800: HSLToHex(h, s, 22),
            900: HSLToHex(h, s, 14),
        };

        // Set CSS variables for each shade
        Object.entries(shades).forEach(([key, value]) => {
            root.style.setProperty(`--blue-${key}`, value);
        });

    }, [primaryColor]);
    const value = {
        primaryColor,
        setPrimaryColor,
    };

    return (
        <ColorContext.Provider value={value}>
            {children}
        </ColorContext.Provider>
    );
};

const useColor = () => {
    const context = useContext(ColorContext);
    if (!context) {
        throw new Error("useColor must be used within a ColorProvider");
    }
    return context;
};

export { ColorProvider, useColor, ColorContext };